<template>
  <div class="goods-list">
     <section class="goods-list-box" v-for="(item, index) in goods" :key="index"  @click="goodindexClick(item)">
       <div class="goods-list-pad">
           <div class="goods-list-img"><img v-lazy="showImage(item)" @load="imgLoad"></div>
           <div class="goods-list-title"><h3>{{item.title}}</h3></div>
           <div class="goods-list-item">
               <span class="goods-list-pre">¥{{item.price}}</span>
               <span class="goods-list-str">{{item.cfav}}</span>
           </div>
       </div>
     </section>
  </div>
</template>

<script>
export default {
  name: "GoodsList",
  props:{
    goods: {
      type: Array,
      default() {
        return [];
      }
    },
  },
  methods:{
    imgLoad() {
      // 发射事件总线
      this.$bus.$emit("imgLoad");
    },
     // 跳转到详情页
     goodindexClick(item){
         if(item.iid){
            this.$router.push(`/page/${item.iid}`);
            //this.$router.push('/page/' + this.goodsItem.iid)
         }
     },
    // 动态显示图片
    showImage(item) {
      return item.img || item.image || item.show.img;
    }

  }

};
</script>
<style lang="scss" scoped>
.goods-list{
    position: relative;
    box-sizing: border-box;
    display: flex;
    -webkit-box-orient: horizontal;
    flex-flow: row wrap;
    align-content: flex-start;
    justify-content: space-between;
    flex-shrink: 0;
    margin-left:8px;
    margin-right: 8px;
    .goods-list-box{
        position: relative;
        box-sizing: border-box;
        display: inline-flex;
        -webkit-box-orient: vertical;
        flex-direction: column;
        align-content: flex-start;
        flex-shrink: 0;
        width: 49%;
        margin-top: 5px;
        margin-bottom: 5px;
        color: rgb(0, 0, 0);
        background-color: rgb(255, 255, 255);
        overflow: hidden;
        .goods-list-pad{
            border: 1.104px solid rgb(237, 237, 237);
            position: relative;
            box-sizing: border-box;
            display: flex;
            -webkit-box-orient: vertical;
            flex-direction: column;
            align-content: flex-start;
            flex-shrink: 0;
            cursor: pointer;
            width:100%;
            padding: 0 5px;
        }
        .goods-list-img{
            height: 250px;
            padding-top: 5px;
            img{
                width: 100%;
                height: 100%;
            }
        }
        .goods-list-title{
            position: relative;
            box-sizing: border-box;
            display: flex;
            -webkit-box-orient: vertical;
            flex-direction: column;
            align-content: flex-start;
            flex-shrink: 0;
            h3{
                white-space: pre-wrap;
                position: relative;
                box-sizing: border-box;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                flex-direction: column;
                align-content: flex-start;
                flex-shrink: 0;
                font-size: 14px;
                text-align: left;
                line-height:1.2;
                height:32px;
                margin-top:5px;
                margin-bottom:5px;
                color: rgb(62, 62, 62);
                -webkit-line-clamp: 2;
                overflow: hidden;
            }
        }
        .goods-list-item{
            position: relative;
            box-sizing: border-box;
            display: flex;
            -webkit-box-orient: horizontal;
            flex-direction: row;
            align-content: flex-start;
            flex-shrink: 0;
            -webkit-box-align: center;
            align-items: center;
            height: 34px;
            justify-content: space-between;
        }
        .goods-list-pre{
            white-space: pre-wrap;
            position: relative;
            box-sizing: border-box;
            display: block;
            -webkit-box-orient: vertical;
            flex-direction: column;
            align-content: flex-start;
            flex-shrink: 0;
            font-size: 17.664px;
            text-align: left;
            font-weight: 600;
            color: rgb(255, 0, 0);
            line-height: 1.05;
        }
        .goods-list-str::before{
            content: "";
            width: 15px;
            height: 15px;
            color: #eee;
            background-size: contain;
            display: inline-block;
            vertical-align: text-bottom;
            background: url(~assets/img/collect.svg) no-repeat 0 0/14px 14px;
        }
    }
}
</style>