<template>
   <div class="tab-menu">
      <nav class="tab-menu-link"  :class="{ active: index === curIndex }"
      @click="tabMenuClick(index)" v-for="(item, index) in tabmenulist" :key="index">
         <span>{{item}}</span>
      </nav>
   </div>
</template>

<script>
export default {
  name: "TabMenu",
  props: {
    tabmenulist: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  data() {
    return {
      curIndex: 0
    };
  },
  methods: {
    tabMenuClick(index) {
      if (this.curIndex === index) return;
         console.log(this.curIndex,"9999")
        this.curIndex = index;
        this.$emit("tabClick", index);
      }
  }
};
</script>
<style lang="scss" scoped>
.tab-menu{
   line-height: 40px;
   display: flex;
   justify-content: space-between;
   height: 40px;
   text-align: center;
  .tab-menu-link{
    flex: 1;
   }

   .tab-menu-link span {
    font-size: 15px;
    padding: 5px;
   }

   .active {
    color: #ff5777;
   }

   .active span {
     border-bottom: 3px solid #ff5777;
   }
}
</style>
